:root {
  --accent-color: #ff6b08;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header-index {
  height: 230px;
  background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("header-main.b2afa59c.jpeg");
}

.header-library {
  height: 230px;
  background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("header-library.9905df44.jpeg");
  background-position: 50% 40%;
}

.section {
  padding: 60px 0;
}

.container {
  width: 882px;
  margin: 0 auto;
}

.list {
  list-style: none;
}

.header {
  height: 100%;
  flex-direction: column;
  justify-content: center;
  gap: 37px;
  display: flex;
}

.navigation {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.navigation__logo {
  align-items: center;
  gap: 10px;
  text-decoration: none;
  display: flex;
}

.navigation__logo--icon {
  stroke: #818181;
  fill: none;
  width: 24px;
  height: 24px;
}

.navigation__logo--text {
  color: #fff;
  text-align: center;
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.navigation__menu {
  gap: 29px;
  display: flex;
}

.navigation__item {
  cursor: pointer;
  position: relative;
}

.navigation__link {
  color: #fff;
  text-transform: uppercase;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.navigation__link--current:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: var(--accent-color);
  display: flex;
  position: absolute;
}

.header__buttons {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.header__button {
  width: 136px;
  height: 44px;
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .3s, color .3s;
}

.header__button:hover, .header__button:focus, .header__button--active {
  background-color: var(--accent-color);
  color: #fff;
  background: var(--accent-color);
  box-shadow: 0px 8px 43px 0px var(--accent-color);
  border: none;
}

.empty-storage {
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

.empty-storage__image {
  width: 200px;
}

.form {
  height: 44px;
  justify-content: center;
  display: flex;
  position: relative;
}

.form__input {
  width: 336px;
  height: 22px;
  color: #fff;
  background: none;
  border: none;
  border-bottom: .5px solid #fff;
}

.form__icon {
  stroke: #fff;
  fill: none;
  width: 12px;
  height: 12px;
}

.form__input:focus {
  border: none;
  border-bottom: .5px solid var(--accent-color);
  width: 336px;
  height: 22px;
  color: #fff;
  background: none;
}

.form__button {
  color: #fff;
  background: none;
  border: none;
  position: absolute;
  top: 2px;
  right: 278px;
}

.movies-gallery {
  min-height: calc(100vh - 800px);
  flex-wrap: wrap;
  gap: 30px;
  display: flex;
}

.movie-card {
  width: 274px;
  position: relative;
  overflow: hidden;
}

.movie-card:hover .movie-card__menu {
  transform: translateY(-120px);
}

.movie-card__menu {
  width: 274px;
  height: 120px;
  background-color: rgba(0, 0, 0, .7);
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform .4s;
  display: flex;
  position: absolute;
  box-shadow: 0 8px 43px #fff;
}

.movie-card__buttons {
  height: 137px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
}

.movie-card__buttons--watched, .movie-card__buttons--queue {
  width: 50px;
  height: 50px;
  fill: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.movie-card__buttons--watched:hover, .movie-card__buttons--queue:hover {
  cursor: pointer;
  fill: var(--accent-color);
}

.movie-card__buttons--watched:focus-visible, .movie-card__buttons--watched:focus-within, .movie-card__buttons--watched:focus, .movie-card__buttons--queue:focus-visible, .movie-card__buttons--queue:focus-within, .movie-card__buttons--queue:focus {
  border: none;
}

.activebutton-queue {
  fill: #ff3d2f;
  animation-name: beat;
  animation-duration: .5s;
  transform: scale(1);
}

.inactivebutton-queue, .inactivebutton-watched {
  fill: #616161;
  animation-name: reversebeat;
  animation-duration: .5s;
  transform: scale(1);
}

.activebutton-watched {
  fill: #1e90ff;
  animation-name: beat;
  animation-duration: .5s;
  transform: scale(1);
}

@keyframes beat {
  0% {
    transform: scale(0);
  }

  20% {
    transform: scale(.3);
  }

  40% {
    transform: scale(.6);
  }

  60% {
    transform: scale(.9);
  }

  80% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes reversebeat {
  0% {
    transform: scale(1);
  }

  20% {
    transform: scale(1.2);
  }

  40% {
    transform: scale(.9);
  }

  60% {
    transform: scale(.6);
  }

  80% {
    transform: scale(.3);
  }

  100% {
    transform: scale(0);
  }
}

.movie-card__poster {
  width: 274px;
  height: 398px;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.movie-card__image {
  width: 274px;
  height: 398px;
  object-fit: cover;
}

.movie-card__text {
  color: #000;
  padding-top: 10px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.movie-card__text--accent {
  color: var(--accent-color);
}

.pagination__list {
  justify-content: center;
  display: flex;
}

.pagination__item {
  width: 40px;
  height: 40px;
  color: #000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.pagination__item :not(.pagination__item--dots) {
  cursor: pointer;
}

.pagination__item--arrow {
  background: #f7f7f7 center no-repeat;
}

.pagination__item--active {
  background: var(--accent-color);
  color: #fff;
}

#back {
  background-image: url("arrow-left.a8a0e214.svg");
}

#next {
  background-image: url("arrow-right.804e21f2.svg");
}

footer {
  background: #f7f7f7;
}

.footer {
  height: 77px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__text {
  color: #545454;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.footer__text--accent {
  color: var(--accent-color);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .movies-gallery {
    width: 768px;
    justify-content: center;
  }

  .container {
    width: 768px;
  }

  .form__button {
    right: 220px;
  }
}

@media screen and (max-width: 767px) {
  .movies-gallery {
    width: 320px;
    justify-content: center;
  }

  .container {
    width: 320px;
  }

  .footer__text {
    font-size: 20px;
    line-height: 35px;
  }

  .form__button {
    right: 30px;
  }

  .form__input {
    width: 270px;
  }
}

